.container {
  display: flex;  
  flex-wrap: wrap;
}
.card {
  margin: 10px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  width: 250px;
}
.card-header img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.card-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 0px 0px 10px 10px;
  min-height: 50px;
}
.tag-container-grid {
  display: flex;
  margin-top: -15px;
  margin-bottom: 10px;
  width: 200px;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.button-container-grid {
  display: flex;
  flex-wrap: wrap;
}
.button-container-grid a{
text-decoration: none;
color: #fff;
width:110px;
height:20px;
font-size: 15px;
background-color:#cccccc;
margin-right: 10px;
}
.tag {
  background: #cccccc;
  border-radius: 50px;
  font-size: 12px;
  margin: 1px;
  color: #fff;
  padding: 2px 10px;
  text-transform: uppercase;
}
.java{
  background: #ee4f33;
}
.nodejs{
  background: #026e00;
}
.js{
  background: #f7df1e;
}
.android{
  background: #a7c93a;
}
.react{
  background: #61DBFB;
}
.mysql{
  background: #005e87;
}
.chakra{
  background: #4fd1c5;
}





:root {
  --backgroundColor: rgba(246, 241, 209);
  --colorShadeA: rgb(106, 163, 137);
  --colorShadeB: rgb(121, 186, 156);
  --colorShadeC: rgb(150, 232, 195);
  --colorShadeD: rgb(187, 232, 211);
  --colorShadeE: rgb(205, 255, 232);
}

.options-container {
  display: grid;
  grid-gap:0,5rem;
  gap:0,5rem;
  grid-auto-flow: dense;
  grid-auto-rows: 3rem;
 grid-template-columns: repeat(auto-fit,minmax(6rem,1fr));
 padding-bottom:10px;
}

button {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  font-size: 0.6rem;
    color:rgb(106, 163, 137);
    color:var(--colorShadeA);
  font-weight: 700;
  text-transform: uppercase;
  font-family: inherit;
}

button.option-button {
   padding: 1em 2em;
   border: 2px solid rgb(106, 163, 137);
   border: 2px solid var(--colorShadeA);
  border-radius: 1em;
  background: rgb(205, 255, 232);
  background: var(--colorShadeE);
transform-style: preserve-3d;
   transition: all 175ms cubic-bezier(0, 0, 1, 1);
}
button.option-button::before {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgb(150, 232, 195);
  background: var(--colorShadeC);
  border-radius: inherit;
    box-shadow: 0 0 0 2px rgb(121, 186, 156), 0 0.75em 0 0 rgb(106, 163, 137);
    box-shadow: 0 0 0 2px var(--colorShadeB), 0 0.75em 0 0 var(--colorShadeA);
 transform: translate3d(0, 0.75em, -1em);
     transition: all 175ms cubic-bezier(0, 0, 1, 1);
}


button.option-button:hover {
  background: rgb(187, 232, 211);
  background: var(--colorShadeD);
  transform: translate(0, 0.375em);
}

button.option-button:hover::before {
  transform: translate3d(0, 0.75em, -1em);
}

button.option-button:active {
            transform: translate(0em, 0.75em);
}

button.option-button:active::before {
  transform: translate3d(0, 0, -1em);
  
      box-shadow: 0 0 0 2px rgb(121, 186, 156), 0 0.25em 0 0 rgb(121, 186, 156);
  
      box-shadow: 0 0 0 2px var(--colorShadeB), 0 0.25em 0 0 var(--colorShadeB);

}
