.social-links {
	display: grid;
	grid-gap:0.5rem;
	gap:0.5rem;
	grid-auto-flow: dense;
	grid-auto-rows: 1rem;
   grid-template-columns: repeat(auto-fit,minmax(1rem,1fr));
}

.social-btn {
	cursor: pointer;
	height: 50px;
	width: 50px;
	font-family: 'Titillium Web', sans-serif;
	color: #333;
	border-radius: 10px;
	box-shadow: 0px 10px 10px rgba(0,0,0,0.1);
	background: white;
	margin: 5px;
	transition: 1s;
}

.social-btn span {
	width: 0px;
	overflow: hidden;
	transition: 1s;
	text-align: center;
}

.social-btn:hover {
	width: 150px;
	border-radius: 5px;
}

.social-btn:hover span {
	padding: 2px;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
}

